.list-header{
  display: flex;
  justify-content: space-between;
  padding: $s-size $l-size;
  background-color: $off-white;
  border:1px solid darken($off-white, 7%); 
  color: $grey;
}

.list-item{
  // display: flex;
  // flex-direction: column;
  // padding: $s-size;
  // // border:1px solid darken($off-white, 7%); 
  // border-top: none;
  // text-decoration: none;
  // color: $dark-grey;
  // transition: background .3s ease;
  // &:hover{
  //   background: $off-white;
  // }
  // @media(min-width: $desktop-breakpoint){
  //   align-items: center;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   padding: $m-size;
  // }
  display: flex;
  justify-content: space-between;
}

.list-item__title{
  margin :0;
  word-break: break-all;
}

.list-item__sub-title{
  color: $grey;
  font-size: $font-size-small;
}

.list-item__data{
  margin: $s-size 0 0 0;
  @media(min-width: $desktop-breakpoint){
    margin: 0;
    padding-left: $s-size;
  }
}

.list-item__message{
  align-items: center;
  justify-content: center;
  color: $grey;
  &:hover{
    background:none;
  }
}

.list-body{
  margin-bottom: 10%;
}

.MuiTableCell-bodyy{
  color: red
}

.list-item_action{
  justify-content: flex-end
}

.item_link{
  text-decoration: none;
  color: white
}