*{
  box-sizing: border-box;
}
.input{
  height: 50px;
  font-weight: 300;
  font-size: $font-size-large
 }
.select{
  @extend .input;
}

.textarea{
  @extend .input;
  height: 10rem;
  width: 100%;
  margin: 0 0 5% 0;
}

.input__long{
  @extend .input ;
  width: 100%
}

.filled-search{
  font-size: 1rem;
}
